"use client";

import React, { useState, useEffect } from "react";
import { useTranslations } from "next-intl";
import { useRouter } from "next/navigation";
import { useAuth } from "@/components/providers/AuthProvider";

export default function LoginPage() {
  const t = useTranslations();
  const router = useRouter();
  const { login, register, isAuthenticated } = useAuth();
  const [isRegister, setIsRegister] = useState(false);
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    if (isAuthenticated) {
      router.push('/en');
    }
  }, [isAuthenticated, router]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");

    try {
      if (isRegister) {
        await register(username, email, password);
      } else {
        await login(email, password);
      }
    } catch (err: any) {
      setError(err.message || "An error occurred");
    }
  };

  return (
    <div className="max-w-md mx-auto mt-8 p-8 bg-gray-900 rounded-lg shadow-xl">
      <h2 className="text-4xl font-bold mb-8 text-center text-white">
        {isRegister ? t("auth.register") : t("auth.welcome")}
      </h2>
      
      {error && (
        <div className="mb-6 p-4 bg-red-500/10 border border-red-500 rounded-lg text-red-500 text-center">
          {error}
        </div>
      )}

      <form onSubmit={handleSubmit} className="space-y-6">
        {isRegister && (
          <div>
            <label htmlFor="username" className="block text-sm font-medium text-gray-300 mb-2">
              {t("auth.username")}
            </label>
            <input
              id="username"
              type="text"
              placeholder={t("auth.username")}
              className="w-full p-4 rounded-lg bg-gray-800 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-white/20 transition-all"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
          </div>
        )}

        <div>
          <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-2">
            {t("auth.email")}
          </label>
          <input
            id="email"
            type="email"
            placeholder={t("auth.email")}
            className="w-full p-4 rounded-lg bg-gray-800 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-white/20 transition-all"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>

        <div>
          <label htmlFor="password" className="block text-sm font-medium text-gray-300 mb-2">
            {t("auth.password")}
          </label>
          <input
            id="password"
            type="password"
            placeholder={t("auth.password")}
            className="w-full p-4 rounded-lg bg-gray-800 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-white/20 transition-all"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>

        <button
          type="submit"
          className="w-full py-4 bg-white text-black font-bold rounded-lg hover:bg-gray-100 transition-colors"
        >
          {isRegister ? t("auth.registerButton") : t("auth.loginButton")}
        </button>
      </form>

      <div className="mt-6 text-center">
        <button
          className="text-gray-400 hover:text-white transition-colors"
          onClick={() => setIsRegister(!isRegister)}
        >
          {isRegister ? t("auth.alreadyAccount") : t("auth.noAccount")}
        </button>
      </div>
    </div>
  );
}
